@import url(https://cdn.jsdelivr.net/npm/bulma@0.9.4/css/bulma.min.css);
.movies{display:grid;grid-template-columns:repeat(auto-fill, minmax(250px, 1fr));grid-gap:20px;gap:20px}

.card-image img{object-fit:contain}

.NewMovie{display:flex;flex-direction:column;grid-gap:20px;gap:20px}.NewMovie__input{position:relative;display:flex;flex-direction:column}.NewMovie__field-error{position:absolute;right:0;bottom:0;-webkit-transform:translateY(100%);transform:translateY(100%)}

.page{display:grid;grid-template:auto / 1fr 400px;grid-column-gap:20px;-webkit-column-gap:20px;column-gap:20px;min-height:100vh}.page-content{padding:20px}.sidebar{padding:20px;border-left:1px solid lightgray}

